body {
  background-color: #8babdc;
}

button.is-hidden {
  display: none;
}

.search-form {
  color: #faebd7;
  background-color: #6284fc;
  justify-content: center;
  margin-bottom: 30px;
  padding: 20px;
  -webkit-text-decoration-color: #faebd7;
  text-decoration-color: #faebd7;
  display: flex;
}

.search-form__input {
  height: 24px;
  background-color: #cef8c2;
  border-radius: 12px;
  margin-right: 10px;
  transition: border-radius .4s cubic-bezier(.445, .05, .55, .95);
}

.search-form__input:hover {
  border-radius: 4px;
}

.search-btn {
  border-radius: 10px;
  transition: scale .4s cubic-bezier(.445, .05, .55, .95);
}

.search-btn:hover {
  scale: 1.1;
}

.load-more-button {
  background-color: #0ff;
  border-style: double;
  border-radius: 16px;
  margin: 20px auto;
  padding: 10px;
  transition: scale .4s cubic-bezier(.445, .05, .55, .95);
  display: flex;
}

.load-more-button:hover {
  scale: 1.1;
}

.gallery {
  grid-gap: 26px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  display: grid;
}

.gallery__link {
  color: #000;
  text-decoration: none;
  list-style: none;
}

.photo-card {
  background-color: #b8c2ea;
  border-radius: 4px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
  box-shadow: 0 3px 8px rgba(0, 0, 0, .24);
}

.info {
  justify-content: center;
  display: flex;
}

.info-item {
  margin-right: 10px;
}

.small-img {
  width: 100%;
  height: 330px;
  display: block;
}

